














import {Component, Emit, Prop, Vue, Watch} from "vue-property-decorator";

@Component({
})
export default class Paginate extends Vue {
    prevPage = 0
    nextPage = 0
    allowPrevPage = false
    allowNextPage = true

    @Prop({
        required: true,
        type: Number
    })
    totalPages!: number
    @Prop({
        required: true,
        type: Number
    })
    totalItems!: number
    @Prop({
        required: true,
        type: Number
    })
    itemsOnPage!: number
    @Prop({
        required: true,
        type: Number
    })
    currentPage!: number

    @Watch('totalItems', {immediate: true})
    @Watch('itemsOnPage', {immediate: true})
    @Watch('currentPage', {immediate: true})
    @Watch('totalPages', {immediate: true})
    watchProps(): void {
        this.prevPage = this.currentPage - 1;
        this.nextPage = this.currentPage + 1;

        this.allowPrevPage = this.prevPage >= 0
        this.allowNextPage = this.nextPage <= this.totalPages
    }

    @Emit('onChangePage')
    changePage(page: number): number {
        return page
    }
}
